<template>
    <div class="boxH">
      <div class="header">
        <div class="tit">{{title}}</div>
        <div class="remove"><i class="iconfont">&#xe606    </i>清空记录</div>
        <p class="num">第{{num}}页</p>
      </div>
      <HCenter ref="HCenter" v-on:pressDown="keepDo" v-on:deleteMessg="deleteMessg" v-on:pressLeft="letLeft" :catId="catId" :catCode="catCode"></HCenter>
      <CFirm ref="CFirm" v-on:listernpopup="firm"></CFirm>
      <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
    </div>
</template>

<style scoped="HHist">
  .boxH{
    width: 1200px;
    height: 720px;
    overflow: hidden;
  }
  .header{
    margin-top: 17px;
  }
  .header:after{
    clear: both;
    content: '';
    display: block;
  }
  .tit{
    width: 78px;
    height: 54px;
    color:rgba(255,255,255,0.65);
    float: left;
    font-size: 39px;
  }
  .remove{
    width: 122px;
    height: 50px;
    color: rgba(255,255,255,1);
    border-radius: 15px;
    margin-left: 29px;
    float: left;
    border: 1px solid #fff;
    text-align: center;
    line-height: 50px;
    background: rgba(255,255,255,0.21);
  }
  .remove.hover{
    background: #13A1C5;
  }
  .num{
    font-size: 24px;
    margin-top: 71px;
    margin-right: 90px;
    color: rgba(255,255,255,0.65);
    float: right;
  }

</style>

<script>
import HCenter from '../history/History_center.vue'
import CFirm from '../Collect/CollFirm.vue'
import {mapGetters} from 'vuex'
import {HTTPUtil as api} from '../fetch/api.js'
import {client as yh} from '../js/client.js'
import {bp as bi} from '../js/ga.js'
    export default {
      props:['type','catId','catCode'],
      data() {
            return {
                title: '历史',
                num:1,
                isShow:false,
                noHistory:false,
            }
        },
      created() {
        this.load();
      },
      methods:{
        keyCode(kc) {
          if (kc == "KeyBack") {
            this.back();
          }else if(kc == "down"){
            this.down();
          }else if(kc == "KeyEnter"){
            this.KeyEnter();
          }
        },
        load(){
          this.$store.commit('GET_HTTPRESPONSETIME',new Date().getTime());
          this.traceId = "|"+this.catId+"|"+'0';
          this.traceType = '|'+this.catCode+"|"+'5';
          this.oldTime =performance.timing.domContentLoadedEventEnd-performance.timing.navigationStart;
          console.log(this.oldTime+'=====页面加载时间');
          bi.loadPage(this.traceId ,this.traceType,this.catCode,0,1,this.traceId ,'',this.oldTime);
          console.log(this.traceId ,this.traceType,this.catCode,0,1,this.traceId ,'',this.oldTime);
        },
        back() {
          this.$router.go(-1);
        },
        down(){
          if(this.noHistory)return;
          this.isShow = false;
          this.removeCss();
          this.$refs.HCenter.updateStast(true, "down", true);
        },
        KeyEnter(){
          this.$refs.HCenter.again(false);
        },
        keepDo(bl,name,pos,num){
          this.num = num;
          console.log('num:'+num)
          console.log('this.num:'+this.num)
          if(!bl){
            this.isShow = true;
            if(pos=='up'){
              this.addCss()
              console.log('里面的this.num:'+this.num)
            }
            if(pos=='KeyEnter'){
              this.$refs.CFirm.updateStast(true,url,"down", true);
            }
          }
        },
        deleteMessg(bl){
          this.noHistory = bl;
        },
        letLeft(bl, name, pos,url,Id,lx,type){
          if (!bl){
            if (pos =='KeyEnter'){
              console.log(bl, name, pos,url,Id,lx,type);
              this.$refs.CFirm.updateStast(true,url,Id,lx,name,type)
            }
          }
        },
        firm(bl,name,pos){
          console.log('bl'+bl+'name'+name+'pos'+pos)
          if (!bl){
            if (name =="CCenter"){
              this.$refs.CCenter.updateStast(true,"remove","KeyEnter");
            }else {
              this.$refs.HCenter.updateStast(true,"remove","KeyEnter");
            }
          }
        },
        addCss(){
         $('.remove').addClass('hover');
        },
        removeCss(){
          $('.remove').removeClass('hover');
        }
      },
        components: {
          HCenter,CFirm
        }
    }
</script>



