<template>
  <div id="app">
    <!-- 这里是需要keepalive的 -->
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"/>
    </keep-alive>
    <!-- 这里不会被keepalive -->
    <router-view v-if="!$route.meta.keepAlive"></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
  created(){
    console.log(navigator.userAgent);
    document.title = "岩华游戏";
  },beforeCreate(){
    let timess = performance.timing.loadEventEnd - performance.timing.loadEventStart;
    if(timess>2000 || timess == 0){
      timess = 2000;
    }
     setTimeout(() => {
        document.getElementsByClassName('openImage')[0].style.display='none';
     }, timess);
   }
}
</script>
<style>
  @import url("./font/iconfont.css");
  *{margin: 0px;padding: 0px;}
  body{position: absolute;width: 1280px;height: 720px;}
  #app{background:linear-gradient(to left bottom, rgba(75,30,57,1) 10%, rgba(28, 44, 69, 1) 50%,rgba(28, 44, 69, 1) 60%,rgba(75,30,57, 0.8) 100%);position: absolute;width: 1200px;height:720px;padding-left:80px;}
  ul li{list-style: none;}
</style>



