
<style type="text/css" scoped="firm">
  .bounce-enter-active {
    -webkit-transform: scale(2);
    -moz-transform: scale(2);
    -ms-transform: scale(2);
    transform: scale(2);
    opacity: 0;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    transition: all 0.3s;
  }
  .bounce-leave-active {
    -webkit-transform: scale(2);
    -moz-transform: scale(2);
    -ms-transform: scale(2);
    transform: scale(2);
    opacity: 0;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    transition: all 0.3s;
  }
  .yy-confirm {
    position: fixed;
    top: 0;
    left: 0;
    width: 1280px;
    height: 720px;
    z-index: 9999999;
  }
  .yy-confirm .bg {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.3;
    z-index: 99999;
  }
  .yy-confirm .yy-container {
    position: fixed;
    display: inline-block;
    top: 184px;
    left: 340px;
    max-width: 1280px;
    z-index: 9999999999999;
    width: 600px;
    border: 1px solid #fff;
  }
  .yy-confirm .yy-container .header {
    height: 50px;
    padding-left: 30px;
    line-height: 50px;
    background-color:rgba(21, 48, 57, 0.9);
    border-bottom: 1px rgba(0,128,128,0.95) solid;
    color: #fff;
    text-align: left;
    box-sizing: border-box;
    margin-top: 0px;
  }
  .yy-confirm .yy-container .yy-footer {
    background: rgba(34,78,96,0.9);
    height: 300px;
    box-sizing: border-box;
  }
  .yy-btn {
    text-decoration: none !important;
    border-radius: 14px;
    text-align: center;
    width: 30%;
    float: left;
    margin-top: 120px;
    margin-left: 80px;
    height: 50px;
    line-height: 50px;
    border: 2px white solid;
    background-color: #152F3A;
    color: #fff;
  }
  .yy-btn.select-on {
    background-color: #992121;
    color: #fff;
  }
  .yy-close-btn {
    cursor: pointer;
    float: right;
  }
</style>

<template>
  <div class="yy-confirm" v-show="visible">
    <div class="bg"></div>
    <div class="yy-container" :style="{width: width}">
      <div class="header">
        <i class="iconfont" style="font-size: 24px;line-height: 50px">&#xe6a3</i>
        {{title.value}}
      </div>
      <slot name="footer">
        <div class="yy-footer" slot="footer">
          <a href="javscript:void(0)" class="yy-btn make-sure select-on">播放详情
            <i class="iconfont" style="font-size: 22px;line-height: 50px">&#xe98c</i>
          </a>
          <a href="javscript:void(0)" class="yy-btn cancel">删除视频
            <i class="iconfont" style="font-size: 22px;line-height: 50px">&#xe606</i>
          </a>
        </div>
      </slot>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import {HTTPUtil as api} from '../fetch/api.js'
  import {client as yh} from '../js/client.js'
  export default {
    data :function() {
      return {
        width: {
          type: String,
          value: '360px'
        },
        title: {
          type: String,
          value:'视频管理'
        },
        isShow:false,
        catCode:"",
        num:0,
        name:"CFirm",
        jsonUrl:'',
        asID:'',
        usename:'',
        zt:true,
        visible: false,
      }
    },
    methods: {
      close () {
        this.$emit('update:visible', false)
      },
      keyCode(kc) {
        if (kc == "left") {
          this.left();
        } else if (kc == "right") {
          this.right();
        }else if(kc == "KeyEnter"){
          this.KeyEnter();
        } else if (kc == "KeyBack") {
          this.KeyBack();
        }
      },
      KeyBack(){
        this.visible = false;
        this.isShow = false;
        this.$emit("listernpopup",false,this.usename,'back');
      }
      ,KeyEnter(){
        this.visible = false;
        this.isShow = false;
        if (this.num == 0){
          if(!this.zt){
            this.$router.push({name:"GamePages",params:{jsonUrl:this.jsonUrl,catCode:this.catCode,catId:0}});
          }else {
            this.$router.push({name:"Special",params:{jsonUrl:this.jsonUrl,catCode:this.catCode,catId:0}});
          }
        }else {
          if (this.usename =="CCenter"){
            if(!this.zt){
              let storage =JSON.parse(localStorage.getItem('PersonInfo'));
              let json =[];
              for(let i = 0;i<storage.length;i++){
                if(i !=this.findindex()){
                  json.push(storage[i]);
                }
              };
              localStorage.setItem( 'PersonInfo',JSON.stringify( json ));
              yh.removeFavorite(this.asID,"");
            }else {
              let storage =JSON.parse(localStorage.getItem('PersonInfo'));
              let json =[];
              for(let i = 0;i<storage.length;i++){
                if(i !=this.findindexs()){
                  json.push(storage[i]);
                }
              };
              localStorage.setItem( 'PersonInfo',JSON.stringify( json ));
              yh.removeFavorite("",this.asID);
            }
          }else{
            yh.removeHistorys(this.asID);
          }
          this.$emit("listernpopup",false,this.usename,'KeyEnter');
        }
      },
      findindex(){
        let storage =JSON.parse(localStorage.getItem('PersonInfo'));
        for(let i = 0;i<storage.length;i++){
          if (storage[i].assetId == this.asID ){
            return i;
          }
        }
      },
      findindexs(){
        let storage =JSON.parse(localStorage.getItem('PersonInfo'));
        for(let i = 0;i<storage.length;i++){
          if (storage[i].specialId == this.asID ){
            return i;
          }
        }
      },
      right(){
        this.num++;
        if (this.num>1){
          this.num=1
        }
        $('.make-sure').removeClass('select-on');
        $('.cancel').addClass('select-on');
      },left(){
        this.num--;
        if (this.num<0){
          this.num=0
        }
        $('.cancel').removeClass('select-on');
        $('.make-sure').addClass('select-on');
      },
      updateStast(bl,url,Id,lx,name,type){
        console.log(bl,url,Id,lx,name,type+'过来的数据');
        this.num=0;
        $('.cancel').removeClass('select-on');
        $('.make-sure').addClass('select-on');
        this.isShow = bl;
        this.visible = bl;
        this.jsonUrl =url;
        this.asID = Id;
        this.catCode = type;
        this.usename = name;
        if (lx=="专题"){
          this.zt = true;
        }else {
          this.zt = false;
        }
      }
    },
    components: {

    }
  }
</script>





