<!--收藏页-->
<style scoped="center_h">
  .move{
    width: 1px;
    height: 460px;
    background: #fff;
    position: absolute;
    right: 30px;
    top: 40px;
  }
  .move .move_box{
    width: 11px;
    height: 56px;
    background: #fff;
    border-radius: 4px;
    position: absolute;
    left: -5.5px;
    top: 0px;

  }
  .imgList ul li .mask{
    color: black;
    position: absolute;
    left: 0;
    top:0;
  }
  .imgList ul{
    margin-left: 10px;

  }
  .imgList{
    position: relative;
    width: 1200px;
    height: 530px;
    overflow: hidden;
  }
  .imgList ul:after{
    content: '';
    clear: both;
    display: block;
  }
  .imgList ul li{
    width: 160px;
    height:230px;
    float: left;
    margin-right: 32px;
    position: relative;
    margin-top: 28px;
    box-sizing: border-box;
    border-radius: 15px;
    border: 1px rgba(255,255,255, 0.5) solid;
  }
  .imgList .imgList_img{
    width: 100%;
    height: 100%;
    border-radius: 15px;
    box-sizing: border-box;

  }
  .imgList ul .tran{
    border: 4px solid #ffffff;
    transform: scale(1.1);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.55);
  }
</style>
<template>
  <div class="imgList" style="box-sizing: border-box">
    <ul>
      <li v-for="(item,index) in itemList"  :key="item.index" :id="('hotImg_'+eleId+'_'+index)" class="imgli animated">
        <lazy-image
          :src='item.assetImg'
          :placeholder='placeholder'
          :initScore = 'initScore'
          :score = "item.score"
          :title = "item.assetName"
          class="imgList_img"
          :imgClass='HistoryImg'
        />
      </li>
    </ul>
    <div class="move" v-if="isLenght">
      <div class="move_box"></div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import {client as yh} from '../js/client.js'
  import {bp as bi} from '../js/ga.js'

  export default {
    name:'CCenter',
    props:['type','catId','catCode'],
    data(){
      return{
        num:0,
        name:'CCenter',
        itemNo:0,
        isShow:true,
        isLenght:false,
        placeholder:require('../assets/img_loading_160x230.png'),
        initScore:require('../assets/corner_mark.png'),
        itemList:[],
        eleId:'',
        pos:'',
        isEnd:false,
        assetId:0,
        HistoryImg:'HistoryImg',
        timer:0,
      }
    },
    computed: {

    },
    created(){
      this.itemList = JSON.parse(localStorage.getItem('PersonInfo'));
      this.itemList.reverse();
      if(this.itemList.length>11){
        this.isLenght = true;
      }
      this.$nextTick(function() {
        this.$lazyImages.loadImage();
      })
      //线上
    },
    mounted(){
      this.addCss();
    },
    methods: {
      keyCode(kc) {
        if (kc == "KeyBack") {
          this.back();
        }else if(kc == "right"){
          this.right();
        }else if(kc == "left"){
          this.left();
        }else if(kc == "down"){
          this.down();
        }else if(kc == "up"){
          this.up();
        }else if(kc == "KeyEnter"){
          this.KeyEnter();
        }
      },
//      点击事件
      collectPage(){
        this.traceId = "|"+this.catId+"|"+0;
        // bp.collectPage = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id,asset_id, trace_id, trace_type, special_type, type)
        bi.collectPage(this.traceId,"|1|5",5,0,1,this.catId,this.assetId,this.traceId,"|1|5",'',this.typeid)
      },
      back() {
        this.$router.go(-1);
        this.clearTimer();
      },
      KeyEnter(){
        console.log('this.itemList:'+this.itemList)
        if(this.itemList === null)return;
        this.isShow = false;
        if (this.itemList[this.itemNo].assetType=="专题"){
          let url = this.itemList[this.itemNo].layoutUrl;
          let Id =this.itemList[this.itemNo].specialId;
          this.assetId = Id;
          this.$emit('pressLeft',this.isShow,this.name,"KeyEnter",url,Id,'专题',"1|6");
        }else {
          let url = this.itemList[this.itemNo].layoutUrl;
          let Id =this.itemList[this.itemNo].assetId;
          this.assetId = Id;
          this.$emit('pressLeft',this.isShow,this.name,"KeyEnter",url,Id,'详情',"1|6");
        }
        this.typeid = 1;
        this.collectPage();
        this.removeCss();
      },
      down(){
        let eleH = this.cEleOffsetTop('#hotImg_'+this.eleId+"_"+this.itemNo+'');
        if(this.itemNo<=this.itemList.length-7) {
          this.removeCss();
          this.itemNo += 6;
          this.addCss();
          if(eleH>600){
            this.num++;
            this.$emit('pressDown', this.isShow,this.name,"up",this.num+1);
            this.gundong();
            $('.imgList ul').stop(false,true).animate({'margin-top': '-='+520},100);
          }
        }else {
          console.log('移动到最后一个了，但是这个是不对的')
          return; //修复最后一行资产继续往下移动会移到最后一个资产
          this.removeCss();
          this.itemNo = this.itemList.length-1;
          this.addCss();
          if(eleH>600){
            if(this.num<(this.itemList.length/12)-1){
              alert('12')
              this.num++;
              this.$emit('pressDown', this.isShow,this.name,"up",this.num+1);
              this.gundong();
              $('.imgList ul').stop(false,true).animate({'margin-top': '-='+520},100);
            }
          }
        }
      },
      scrollName(){
        // $('.gameImg li:eq('+this.itemNo+') .s-title-name')
        let p_title =  $('#hotImg_'+this.eleId+"_"+this.itemNo+' .s-title-name').text();
        if(p_title.length>8){
          let speed = 50;
          let that = this;
          let tab =  $('#hotImg_'+this.eleId+"_"+this.itemNo+' .s-title')[0];
          let tab1 =  $('#hotImg_'+this.eleId+"_"+this.itemNo+' .s-title-name')[0];
          let tab2 =  $('#hotImg_'+this.eleId+"_"+this.itemNo+' .s-title-temp')[0];
          tab.style.textOverflow = 'clip';
          tab2.innerHTML = tab1.innerHTML;
          this.timer = setInterval(function(){
            console.log('要滚动的文字'+p_title)
            that.Marquee(tab,tab1,tab2)
          },speed);
        }
      },
      Marquee(tab,tab1,tab2){
        let a = 0;
        if(tab2.offsetWidth - tab.scrollLeft <= 0){
          // tab.scrollLeft -= tab1.offsetWidth;
          tab.scrollLeft  = 0; // 上面的写法会导致滚动时突然抖动
        }else{
          a = tab.scrollLeft;
          tab.scrollLeft++;
          if(a==tab.scrollLeft){
            tab.scrollLeft=0;
          }
        }
      },
      clearTimer(){
        if(this.timer!=0){
          clearInterval(this.timer);
          this.timer = 0;
          let tab = $('#hotImg_'+this.eleId+"_"+this.itemNo+' .s-title')[0];
          $('#hotImg_'+this.eleId+"_"+this.itemNo+' .s-title-temp')[0].innerHTML="";
          tab.scrollLeft = 0;
          tab.style.textOverflow = 'ellipsis';
        }
      },
      gundong(){
        let number = Math.ceil(this.itemList.length/12);
        let ly =Math.ceil(460/(number-1)-23);
        if(number <= 1){
          ly = 0;
        };
        $('.move_box').css({'transition': 'all 0.4s', 'transform': 'translateY('+ ly*this.num +'px)'});
      },
      up(){
        this.removeCss();
        let eleH = this.cEleOffsetTop('#hotImg_'+this.eleId+"_"+this.itemNo+'');
        if(this.itemNo>5){
          this.itemNo-=6;
          this.addCss();
          if(eleH<500){
            this.num--;
            this.$emit('pressDown', this.isShow,this.name,"up",this.num+1);
            this.gundong();
            $('.imgList ul').stop(false,true).animate({'margin-top': '+='+520},100);
          }
        }else{
          this.isShow = false;
          this.$emit('pressDown', this.isShow,this.name,"up",this.num+1);
        }
      },left(){
        if(this.itemNo==0 ||this.itemNo==6 ||this.itemNo==12)return;
        this.removeCss();
        this.itemNo--;
        this.addCss();
      },right(){
        if(this.itemNo==5||this.itemNo==11 || this.itemNo>=this.itemList.length-1)return;
        this.removeCss();
        this.itemNo++;
        this.addCss();
      },addCss(){
        $('#hotImg_'+this.eleId+"_"+this.itemNo).addClass('tran');
        this.scrollName();
      },removeCss(){
        $('#hotImg_'+this.eleId+"_"+this.itemNo).removeClass('tran');
        this.clearTimer();
      },
      updateStast(bl,pos,name){
        //线上
        this.itemList = JSON.parse(localStorage.getItem('PersonInfo'));
        this.itemList.reverse();
        this.removeCss();
        this.num = 0;
        this.itemNo = 0;
        this.addCss();
        this.isShow = bl;
        this.gundong();
        $('.imgList ul').stop(false,true).animate({'margin-top': 0},100);
         if (name == 'KeyEnter'){
            this.typeid = 2;
            console.log(this.traceId,"|1|5|",5,0,1,this.catId,this.assetId,this.traceId,"|1|5",'',this.typeid);
            this.collectPage();
          }
      },
      again(bl){
        if (!bl){
          yh.removeFavorites();
          localStorage.removeItem("PersonInfo");
          this.itemList = JSON.parse(localStorage.getItem('PersonInfo'));
          this.itemList.reverse();
        }
      },
      cEleOffsetTop(ele){
        let fEleDistance = $(ele).offset();
        let realHeight = fEleDistance.top+300;
        return realHeight;
      },
    },
    components: {

    }
    ,watch:{
//      itemList(){
//        this.num = 0;
//        this.itemNo = 0;
//        let storage =localStorage.getItem('PersonInfo');
//        console.log(storage);
//        this.itemList = JSON.parse(storage);
//        this.eleId = this.itemList[0].assetId;// 默认数据第一个为组件内的id
//        this.name = 'CCenter';// 默认数据第一个为组件名字
//
//      }
    }
  }
</script>




