<template>
  <div class="gameBox">
    <!-- 导航、设置 -->
    <div class="navbar">
      <ul class="programa">
        <li><div class="focus"></div>精彩视频</li>
        <li><div class="focus"></div>热门游戏</li>
        <li><div class="focus"></div>电竞赛事</li>
      </ul>
      <ul class="setting">
        <li><span class="iconfont icon-soushuo" ></span><span class="setter">搜索</span></li>
        <li><span class="iconfont icon-shaixuan"></span><span class="setter">筛选</span></li>
        <li><span class="iconfont icon-lishi"></span><span class="setter">历史</span></li>
        <li><span class="iconfont icon-shoucang1"></span><span class="setter">收藏</span></li>
      </ul>
    </div>
    <!-- 内容推荐区 -->
    <div class="contentBox">
      <div class="scrollBar">
        <!-- 精彩视频 -->
        <div class="greatVideo">
          <div class="gv1">
            <div class="bigVideo"><img :src="recommendedData.club.elementList[0].elementImg"/></div>
            <ul class="gvul">
              <li  v-for="(item,index) in recommendedData.club.elementList" v-if="index>0&&index<5" :key="index" >
                <lazy-image
                  :src='item.elementImg'
                  :placeholder='placeholder'
                  :imgClass='round'
                />
              </li>
            </ul>
          </div>
          <div class="gv2">
            <ul>
              <li v-for="(item,index) in recommendedData.club.elementList" v-if="index>4" :key="index">
                <lazy-image
                  :src='item.elementImg'
                  :placeholder='placeholder'
                  :imgClass='round'
                />
              </li>
            </ul>
          </div>
        </div>
        <!-- 热门游戏 -->
        <div class="hotGame">
          <ul>
            <li v-for="(item,index) in recommendedData.games.elementList" v-if="index<9" :key="index">
              <lazy-image
                :src='item.elementImg'
                :placeholder='placeholder'
                :imgClass='round'
              />
            </li>
          </ul>
        </div>
        <!-- 电竞赛事 -->
        <div class="tournament">
          <ul>
            <li v-for="(item,index) in recommendedData.events.elementList" v-if="index<6" :key="index">
              <lazy-image
                :src='item.elementImg'
                :placeholder='placeholder'
                :imgClass='round'
              />
            </li>
          </ul>
        </div>
      </div>
    </div>
    <HFirm ref="HFirm" v-on:listernpopup="firm" ></HFirm>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>
</template>
<style scoped="home">
  /** 头部 ***/
  .gameBox{overflow: hidden;padding-left: 22px;box-sizing: border-box;}
  .programa,.setting{position: relative;display: inline-block; }
  .programa {margin-right:190px;float: left;margin-top:60px;}
  .programa li,.setting li{float: left;color: #FFFFFF;}
  .programa li{color:#C5C4CB;width: 100px;height: 42px; font-size: 25px;font-family: 'PingFang HK','Medium';margin-right:80px;}
  .programa li:nth-child(3){margin-right: 0px;}
  .programa .focus{margin-top:-5px;height:3px;background-color:#FFFFFF;border-radius: 5px 5px 0px 0px;margin-bottom:2px;display:none;}
  .programa .focus:after{content: '';position: relative;border: 6px solid #FFFFFF;border-color: #FFFFFF transparent transparent;width: 0;height: 0;bottom: -9px;left: 50%;margin-left: -6px;}
  .setting{margin-top:60px;}
  .setting li{background-color:#777182;margin-left:3px;text-align: center;width: 115px;height: 44px;line-height:44px;border: 1px solid #707070; font-size: 20px;font-family:'Helvetica Neue', 'Regular';}
  .setting li:nth-child(1){border-radius: 12px 0px 0px 12px;}
  .setting li:nth-child(4){border-radius: 0px 12px 12px 0px;}
  .setter{margin-left:10px;}
  .setting li.hover{background-color: #2CA1C9;}
  .scrollBar{position: relative;left:0px;}
  /*** 内容 ***/
  .greatVideo,.hotGame,.tournament{width: 1280px;box-sizing: border-box;float: left;height: 100%;}
  .greatVideo img,.hotGame img,.tournament img{width: 100%;height: 100%;border-radius: 15px;}
  .contentBox{width:4000px;height:500px;margin-top: 27px;}
  .gv1{height: 306px;}
  .gv1 .bigVideo{position:relative;z-index: 99;}
  .bigVideo{height: 100%;;width: 544px;border-radius:15px;float: left;}
  .gvul{margin-left: 32px;height: 100%;width:600px;display: inline-block;box-sizing: border-box;position: relative;z-index:10}
  .gvul li{width: 256px;height: 144px;border-radius: 15px;float: left;}
  .gvul li div.imglazy{border-radius: 15px;}
  .gvul li:nth-child(1){margin-right: 34.5px;margin-bottom: 18px;}
  .gvul li:nth-child(2){margin-bottom: 18px;}
  .gvul li:nth-child(3){margin-right: 34.5px;}
  .gv2{margin-top:31px;width: 1200px;height: 142px;}
  .gv2 ul{display: inline-block;}
  .gv2 li{float: left;background-color: darkolivegreen;width:256px;height:142px;margin-right:34px;border-radius:15px; }
  .gv2 li:nth-child(2){margin-right:30px;}
  .gv2 li:nth-child(3){margin-right:37px;}
  .gv2 li div.imglazy{border-radius: 15px;}
  .hotGame ul li{width: 352px;height: 142px;border-radius:15px;float: left;background-color: grey;margin-bottom:33px;margin-right: 32px;}
  .tournament ul li{width: 352px;height:230px;border-radius:15px;float: left;background-color: grey;margin-bottom:30px;margin-right: 32px;}
  /**** 焦点选中状态 ****/
  .programa li.color{color:#FFFFFF}
  .gvul li.change{border: 4px solid #FFFFFF;margin:-3px 30px 13px -3px;border-radius: 17px;box-shadow:0px 10px 20px rgba(0, 0, 0, 0.55);transform: scale(1.07);}
  .gv1 div.change{border: 4px solid #FFFFFF;margin:-4px;border-radius: 17px;box-shadow:0px 10px 20px rgba(0, 0, 0, 0.55);transform: scale(1.07);}
  .gv2 li.change{border: 4px solid #FFFFFF;margin:-3px 29px -3px -3px;border-radius: 17px;box-shadow:0px 10px 20px rgba(0, 0, 0, 0.55);transform: scale(1.07);}
  .gv2 li:nth-child(2).change{border: 4px solid #FFFFFF;margin:-3px 25px -3px -3px;border-radius: 17px;box-shadow:0px 10px 20px rgba(0, 0, 0, 0.55);transform: scale(1.07);}
  .gv2 li:nth-child(3).change{border: 4px solid #FFFFFF;margin:-3px 32px -3px -3px;border-radius: 17px;box-shadow:0px 10px 20px rgba(0, 0, 0, 0.55);transform: scale(1.07);}
  .hotGame li div{border-radius: 15px;border:1px solid #FFFFFF;}
  .hotGame li.change{border: 4px solid #FFFFFF;margin:-4px 28px 29px -4px;border-radius: 20px;box-shadow:0px 10px 20px rgba(0, 0, 0, 0.55);transform: scale(1.07);}
  .tournament li div{border-radius: 15px;border:1px solid #FFFFFF;}
  .tournament li.change{border: 4px solid #FFFFFF;margin:-4px 28px 26px -4px;border-radius: 20px;box-shadow:0px 10px 20px rgba(0, 0, 0, 0.55);transform: scale(1.07);}

</style>
<style>
  .round{border-radius: 15px;}
</style>

<script>
  import {HTTPUtil as api} from '../fetch/api.js';
  import {mapGetters} from 'vuex'
  import {client as yh} from '../js/client.js'
  import {bp as bi} from '../js/ga.js'
  import c from '../js/common.js'
  import HFirm from '../page/HomeFirm.vue'
  export default {
    name: 'gameBox',
    data () {
      return {
        msg: 'TV web game',
        recommendedData:{
          club:{elementList:[{elementImg:require('../assets/img_loading_352x190.png')}]},
          games:{elementList:[{elementImg:""}]},
          events:{elementList:[{elementImg:""}]}
        },
        pos:0,
        itemNo:0,
        isShow:true,
        placeholder:require('../assets/img_loading_352x190.png'),
        round:'round',
      }
    },components:{
      HFirm
    },activated(){

    },created(){
      this.$store.commit('GET_STARTTIME',new Date().getTime());
      if (c.isAllNull(c.getLocal("GamesTheme"))) {
        let _this = this;
        console.log("rootPath:"+this.rootPath);
        let _h = this.rootPath.indexOf('https');
        if( _h >= 0){
          this.$store.commit('GET_ISHTTPS',true);
          localStorage.setItem("ISHTTPS", true);
        }
        api.getTheme(this.rootPath).then((json) => {
          _this.$store.commit('GET_PROGRAMA', json.data);
          localStorage.setItem("GamesTheme", JSON.stringify(json.data));
          var _cjson = _this.catList.jsonUrl;
          if (!c.isAllNull(_cjson)) { // 获取成功以后立马获取二级栏目数据
            if(_this.isHTTPS && _cjson.indexOf('https')<0 ){
              _cjson =  _cjson.replace('http','https');
            }
            let curl =  _cjson + this.behindParams;
            alert(curl)
            api.get(curl).then((json)=>{
              if (200 == json.code || "200" == json.code) {
                console.log("获取二级栏目数据成功:" + json.data.catList);
                console.log(json.data.catList);

                _this.navBar = json.data.catList;
                _this.$store.commit('GET_CATEGORY', json.data.catList)
                localStorage.setItem("G-Category", JSON.stringify(json.data.catList));
                _this.getInitData();
              }
              console.log("初始化主题及栏目数据成功,写入缓存成功");
            },(json)=>{
              console.log("请求二级栏目数据失败",json);
            })

          }

        },(json)=>{
          console.log("请求主题数据失败",json);
        });
      } else {
        console.log("use cache");
      }
    },mounted() {
      c.startTime();
      if (!c.isAllNull(c.getLocal("GamesTheme"))) {
          this.getInitData();
      }
      bi.openApp(); // bi数据埋点
      console.log("岩华TV游戏版");
      console.log("组件初始化完成");
      console.log("组件模块加载完毕...");
      console.log("懒加载初始化开始");
      console.log("版本:v1.1.03");
      console.log("终端信息:"+window.navigator.appVersion);
      console.log("userAgent:"+window.navigator.userAgent);
      this.$nextTick(function(){
        this.addCss();
      });
    },methods:{
      getInitData(){ // 初始化页面请求，获取精彩视频+热门游戏+电竞赛事
        api.get(this.category[0].jsonUrl + this.behindParams).then((jsonData)=>{
          if (200 == jsonData.code || "200" == jsonData.code) {
            let jsonArray = jsonData.data;
            this.recommendedData.club = jsonArray.specialList[0];
            this.recommendedData.games = jsonData.data.specialList[1];
            this.recommendedData.events = jsonData.data.specialList[2];
          }
        })
        this.$nextTick(function(){
            try {
                 let overTime = new Date().getTime() - this.startTime;
                 console.log("首屏渲染耗时:"+overTime);
                 bi.loadPage(0,0,1,this.recommendedData.club.specialId,0,0,0,overTime);
            } catch (error) {
                console.log(error);
            }
        })
      },
      keyCode(kc){
        if (kc == "KeyBack") {
          this.back();
        }else if(kc == "right"){
          this.right();
        }else if(kc == "left"){
          // if($('.scrollBar').is(":animated")){return}
          this.left();
        }else if(kc == "down"){
          this.down();
        }else if(kc == "up"){
          this.up();
        }else if(kc == "KeyEnter"){
//              let url = this.recommendedData;
//              console.log(url);
//              this.catid = this.eleList[0].elementList[this.number].elementId;
//              this.$router.push({name:"GamePages",params:{jsonUrl:url,catCode:this.traceType,catId:this.tra,parent:8,parentId:this.pageId}});
          if(this.pos==1){
            if(this.itemNo==0){
              this.$router.push({name:'search',params:{catId:this.recommendedData.events.specialId,catCode:this.recommendedData.events.specialName}});
            }else if(this.itemNo==2){
              this.$router.push({name:'Hhistory',params:{catId:this.recommendedData.events.specialId,catCode:this.recommendedData.events.specialName}});
            }else if (this.itemNo==3){
              this.$router.push({name:'CHome',params:{catId:this.recommendedData.events.specialId,catCode:this.recommendedData.events.specialName}});
            }else if(this.itemNo==1) {
              this.$router.push({name: 'filter',params:{catId:this.recommendedData.events.specialId,catCode:this.recommendedData.events.specialName}});
            }
          }else if(this.pos==2){
              try {
                  bi.categoryClick(this.recommendedData.club.specialId, this.recommendedData.club.elementList[this.itemNo].elementId, 3, 1, this.recommendedData.club.specialId, 0, 0);
              } catch (error) {
                  console.log(error);
              }
            let curl  = this.recommendedData.club.elementList[this.itemNo].jsonUrl;
            this.$router.push({name:'GamePages',params:{jsonUrl:curl,catId:"|"+this.recommendedData.events.specialId,catCode:1,parent:1,parentId:this.recommendedData.events.specialId}});
          }else if(this.pos==3){
             try {
                  bi.categoryClick(this.recommendedData.games.specialId, this.recommendedData.games.elementList[this.itemNo].elementId, 3, 1, this.recommendedData.games.specialId, 0, 0);
              } catch (error) {
                  console.log(error);
              }
            let curl  = this.recommendedData.games.elementList[this.itemNo].jsonUrl;
            this.$router.push({name:'Special',params:{jsonUrl:curl,catId:this.recommendedData.events.specialId,catCode:1}});
          }else if(this.pos==4){
            try {
                  bi.categoryClick(this.recommendedData.events.specialId, this.recommendedData.events.elementList[this.itemNo].elementId, 3, 1, this.recommendedData.events.specialId, 0, 0);
            } catch (error) {
                  console.log(error);
            }
            let curl  = this.recommendedData.events.elementList[this.itemNo].jsonUrl;
            this.$router.push({name:'Special',params:{jsonUrl:curl,catId:this.recommendedData.events.specialId,catCode:1}});
          }
        }
      },back() {
        //this.$router.go(-1);
        this.$refs.HFirm.updateStast(true);
        this.isShow = false;
      },
      firm(bl){
        if (bl){// 退出应用
          let st = new Date().getTime();
          let end = st - c.getAppStartTime();
          console.log('停留时间'+end);
          bi.closeApp(this.recommendedData.club.specialId,end);
          yh.back();
        }else {
          this.isShow = true;
        }
      },right(){
        if(this.pos==0){
          if(this.itemNo==2){
            this.removeCss();
            this.pos = 1;
            this.itemNo = 0;
            this.addCss();
            return;
          }
          this.removeCss();
          this.itemNo++;
          this.addCss();
        }else if(this.pos == 1){
          if(this.itemNo>2)return;
          this.removeCss();
          this.itemNo++;
          this.addCss();
        }else if(this.pos == 2){
          this.removeCss();
          if(this.itemNo==2){
            this.pos=3;
            this.itemNo=0;
            $('.scrollBar').stop(false,true).animate({'left':'-1280px'},200);
          }else if(this.itemNo==4){
            this.pos=3;
            this.itemNo=3;
            $('.scrollBar').stop(false,true).animate({'left':'-1280px'},200);
          }else if(this.itemNo==8){
            this.pos=3;
            this.itemNo=6;
            $('.scrollBar').stop(false,true).animate({'left':'-1280px'},200);
          }else{
            this.itemNo++;
          }
          this.addCss();
        }else if(this.pos==3){
          this.removeCss();
          if(this.itemNo==2||this.itemNo==5){
            this.pos=4;
            this.itemNo = 0;
            $('.scrollBar').stop(false,true).animate({'left':'-2560px'},200);
          }else if(this.itemNo==8){
            this.pos=4;
            this.itemNo = 3;
            $('.scrollBar').stop(false,true).animate({'left':'-2560px'},200);
          }else{
            this.itemNo++;
          }
          this.addCss();
        }else if(this.pos==4){
          if(this.itemNo==5)return;
          this.removeCss();
          this.itemNo++;
          this.addCss();
        }
      },left(){
        if(this.pos==0){
          if(this.itemNo==0)return;
          this.removeCss();
          this.itemNo--;
          this.addCss();
        }else if(this.pos == 1){
          if(this.itemNo==0){
            this.removeCss();
            this.pos = 0;
            this.itemNo = 2;
            this.addCss();
            return;
          }
          this.removeCss();
          this.itemNo--;
          this.addCss();
        }else if(this.pos == 2){
          if(this.itemNo==0)return;
          this.removeCss();
          if(this.itemNo==3){
            this.itemNo=0;
          }else{
            this.itemNo--;
          }
          this.addCss();
        }else if(this.pos==3){
          this.removeCss();
          if(this.itemNo==0){
            this.pos=2;
            this.itemNo=2;
            $('.scrollBar').stop(false,true).animate({'left':'0px'},200);
          }else if(this.itemNo==3){
            this.pos=2;
            this.itemNo=4;
            $('.scrollBar').stop(false,true).animate({'left':'0px'},200);
          }else if(this.itemNo==6){
            this.pos=2;
            this.itemNo=8;
            $('.scrollBar').stop(false,true).animate({'left':'0px'},200);
          }else{
            this.itemNo--;
          }
          this.addCss();
        }else if(this.pos==4){
          this.removeCss();
          if(this.itemNo==0){
            this.pos=3;
            this.itemNo=2;
            $('.scrollBar').stop(false,true).animate({'left':'-1280px'},200);
          }else if(this.itemNo==3){
            this.pos=3;
            this.itemNo=8;
            $('.scrollBar').stop(false,true).animate({'left':'-1280px'},200);
          }else{
            this.itemNo--;
          }
          this.addCss();
        }
      },down(){
        if(this.pos==0){
          this.removeCss();
          if(this.itemNo==0){
            this.pos = 2;
          }else if(this.itemNo==1){
            this.pos = 3;
          }else if(this.itemNo==2){
            this.pos = 4;
          }
          this.itemNo = 0;
          this.addCss();
        }else if(this.pos==1){
          this.removeCss();
          this.pos = 4;
          this.itemNo=0;
          this.addCss();
        }else if(this.pos==2){
          this.removeCss();
          if(this.itemNo==1){
            this.itemNo=3;
          }else if(this.itemNo==2){
            this.itemNo=4;
          }else if(this.itemNo==0){
            this.itemNo=5;
          }else if(this.itemNo==3){
            this.itemNo=7;
          }else if(this.itemNo==4){
            this.itemNo=8;
          }
          this.addCss();
        }else if(this.pos==3){
          if(this.itemNo==6||this.itemNo==7||this.itemNo==8)return;
          this.removeCss();
          this.itemNo+=3;
          this.addCss();
        }else if(this.pos==4){
          this.removeCss();
          if(this.itemNo==0||this.itemNo==1||this.itemNo==2){
            this.itemNo+=3;
          }
          this.addCss();
        }
      },up(){
        if(this.pos==2){
          this.removeCss();
          if(this.itemNo==0||this.itemNo==1||this.itemNo==2){
            this.pos = 0;
            this.itemNo = 0;
          }else if(this.itemNo==3||this.itemNo==4){
            this.itemNo-=2;
          }else if(this.itemNo==5||this.itemNo==6){
            this.itemNo=0;
          }else if(this.itemNo==7){
            this.itemNo=3;
          }else if(this.itemNo==8){
            this.itemNo=4;
          }
          this.addCss();
        }else if(this.pos==3){
          this.removeCss();
          if(this.itemNo==0||this.itemNo==1||this.itemNo==2){
            this.pos=0;
            this.itemNo=1;
          }else{
            this.itemNo-=3;
          }
          this.addCss();
        }else if(this.pos==4){
          this.removeCss();
          if(this.itemNo==0||this.itemNo==1||this.itemNo==2){
            this.pos=0;
            this.itemNo=2;
          }else{
            this.itemNo-=3;
          }
          this.addCss();
        }
      },addCss(){
        if(this.pos==0){
          $('.programa li').eq(this.itemNo).addClass('color');
          $('.focus').eq(this.itemNo).show();
          if(this.itemNo==0){
            $('.scrollBar').stop(false,true).animate({'left':'0px'},200);
          }else if(this.itemNo==1){
            $('.scrollBar').stop(false,true).animate({'left':'-1280px'},200);
          }else if(this.itemNo==2){
            $('.scrollBar').stop(false,true).animate({'left':'-2560px'},200);
          }
        }else if(this.pos==1){
          $('.setting li').eq(this.itemNo).addClass('hover');
        }else if(this.pos == 2){
          if(this.itemNo == 0){
            $('.bigVideo').addClass('change');
          }else if(this.itemNo>0&&this.itemNo<5){
            $('.gvul li').eq(this.itemNo-1).addClass('change');
          }else if(this.itemNo){
            $('.gv2 li').eq(this.itemNo-5).addClass('change');
          }
        }else if(this.pos==3){
          $('.hotGame li').eq(this.itemNo).addClass('change');
        }else if(this.pos==4){
          $('.tournament li').eq(this.itemNo).addClass('change');
        }
      },removeCss(){
        if(this.pos==0){
          $('.programa li').eq(this.itemNo).removeClass('color');
          $('.focus').eq(this.itemNo).hide();
        }else if(this.pos==1){
          $('.setting li').eq(this.itemNo).removeClass('hover');
        }else if(this.pos==2){
          if(this.itemNo==0){
            $('.bigVideo').removeClass('change');
          }else if(this.itemNo>0&&this.itemNo<5){
            $('.gvul li').eq(this.itemNo-1).removeClass('change');
          }else if(this.itemNo){
            $('.gv2 li').eq(this.itemNo-5).removeClass('change');
          }
        }else if(this.pos==3){
          $('.hotGame li').eq(this.itemNo).removeClass('change');
        }else if(this.pos==4){
          $('.tournament li').eq(this.itemNo).removeClass('change');
        }
      }
    },computed: {
      ...mapGetters([
        'rootPath',
        'catList',
        'category',
        'isHTTPS',
        'behindParams',
        'startTime',
        'httpRequestTime',
        'httpResponseTime',
      ])
    },watch:{

    },beforeRouteLeave(to, from, next){
      try {
        let overTime = 0;
           overTime =  new Date().getTime() - this.startTime;
           this.$store.commit('GET_STARTTIME',new Date().getTime());
           bi.closPage(this.focusId,1,1,this.focusId,0,0,0,overTime)
           console.log("页面停留时间："+overTime+"^^^^^^^focusId:"+this.focusId);
      } catch (error) {
           console.log(error);
      }finally{
           next();
      }
    }
  }
</script>



