import Vue from 'vue'
import Router from 'vue-router'
import home from '@/page/homePage'
import Hhistory from '../history/History_history.vue'
import HCenter from '../history/History_center.vue'
import Special from '../Special/specialHome.vue'
import GamePages from '../DetailPages/GamePages.vue'
import GameTop from '../DetailPages/GameTop.vue'
import GameList from '../DetailPages/GameList.vue'
import GameRecom from '../DetailPages/GameRecom.vue'
import CHome from '../Collect/CollectHome.vue'
import CCenter from '../Collect/CollectCenter.vue'
import CFirm from '../Collect/CollFirm.vue'
import HFirm from '../page/HomeFirm.vue'

const LFirm = () => import('../tool/Firm/Lfirm.vue');
const Search = () => import('../page/search/Search.vue');
const Filter = () => import('../page/filter/Sizer.vue');





Vue.use(Router);



export default new Router({
  base: '/game/',
  mode: 'history',
  routes: [
    { path: '/', name: 'index', component: home,props: true, meta: {keepAlive: true }},
    { path: '/Hhistory', name: 'Hhistory', component: Hhistory, props: true, meta: { keepAlive: false }},
    { path: '/HCenter', name: 'HCenter', component: HCenter, props: true, meta: { keepAlive: false }},
    { path: '/search', name: 'search', component: Search, props: true, meta: { keepAlive: true } },
    { path: '/filter', name: 'filter', component: Filter, props: true, meta: { keepAlive: true } },
    {
      path:'/hotTopics',
      name:'Special',
      component:Special,
      props: true,
      meta: { keepAlive: false }
    },
    {
      path:'/detail',
      name:'GamePages',
      component:GamePages,
      props: true,
      meta: { keepAlive: false }
    },
    {
      path:'/GameTop',
      name:'GameTop',
      component:GameTop,
      props: true, meta: { keepAlive: false }
    },
    {
      path:'/GameList',
      name:'GameList',
      component:GameList
      , props: true, meta: { keepAlive: false }
    },
    {
      path:'/GameRecom',
      name:'GameRecom',
      component:GameRecom
      , props: true, meta: { keepAlive: false }
    },
    {
      path:'/CHome',
      name:'CHome',
      component:CHome
      , props: true, meta: { keepAlive: false }
    },
    {
      path:'/CCenter',
      name:'CCenter',
      component:CCenter
      , props: true, meta: { keepAlive: false }
    },
    {
      path:'/CFirm',
      name:'CFirm',
      component:CFirm,
      props: true,
      meta: { keepAlive: false }
    },
    {
      path:'/HFirm',
      name:'HFirm',
      component:HFirm,
      props: true,
      meta: { keepAlive: false }
    },
    {
      path:'/LFirm',
      name:'LFirm',
      component:LFirm,
      props: true,
      meta: { keepAlive: false }
    }

  ]
})



