<!--详情页——相似推荐-->
<style scoped="Masse">
  .imgList ul li .mask{
    color: black;
    position: absolute;
    left: 0;
    top:0;
  }
  .lick{
    font-family:PingFangSC-Semibold;
    font-size:22px;
    color:rgba(255,252,252,0.66);
    text-align:left;
  }
  .imgList{
    position: relative;
    width: 1200px;
  }
  .imgList ul:after{
    content: '';
    clear: both;
    display: block;
  }
  .imgList ul li{
    width: 160px;
    height:230px;
    float: left;
    margin-left: 16px;
    margin-right: 16px;
    position: relative;
    margin-top: 18px;
    box-sizing: border-box;
    border: 1px rgba(255,255,255, 0.5) solid;
    border-radius: 15px;
  }
  .imgList .imgList_img{
    width: 100%;
    height: 100%;
    box-sizing: border-box;

  }
  .imgList ul .tran{
    border: 4px solid #ffffff;
    transform: scale(1.1);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.55);
    border-radius: 19px;
  }
</style>
<template>
  <div class="imgList" style="box-sizing: border-box;margin-top: 20px">
    <p class="lick">{{title}}</p>
    <ul class="gameImg">
      <li  v-for="(item,index) in itemList"  :key="item.id" :id="('hotImg_'+eleId+'_'+index)" class="imgli animated"
           v-if="index<=5" >
        <!-- <img :src="item.assetImg" class="imgList_img" > -->
        <!--<lazy-image-->
        <!--:src='item.assetImg'-->
        <!--:placeholder='placeholder'   class="imgList_img"/>-->
        <lazy-image
          :src='item.assetImg'
          :placeholder='placeholder'
          :initScore = 'initScore'
          :score = "item.score"
          :title = "item.assetName"
          class="imgList_img"
          :imgClass='HistoryImg'
        />
      </li>
    </ul>
    <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import { client as yh} from '../js/client.js'
  import {bp as bi} from '../js/ga.js'
  export default {
    props:['initData',"catCode","catId","parent","parentId"],
    data(){
      return{
        name:'Movie_assetList',
        itemNo:0,
        isShow:false,
        placeholder:require('../assets/img_loading_160x230.png'),
        initScore:require('../assets/corner_mark.png'),
        HistoryImg:'HistoryImg',
        isEnd:false,
        eleId:'',
        pos:'',
        itemList:[],
        arrc:[],
        timer:0,
        specialList:[
          {
            actorImg:"",
            actorId:"",
            actorName:"",
            actorType:"",
            jsonUrl:"",
          }
        ]
      }
    },
    computed: {
      ...mapGetters([
        'behindParams',
      ])
    },
    created(){
      console.log("游戏版顶部详情页初始化")
      this.specialList = this.initData.data.assetList;
      this.eleId = this.initData.data.assetList[0].assetId;// 默认数据第一个为组件内的id
      this.name = "GameRecom";// 默认数据第一个为组件名字
      this.title = "相似推荐";// 默认标题
      this.itemList = this.specialList.sort(function(){ return 0.5 - Math.random() });
      this.$nextTick(function() {
        this.$lazyImages.loadImage();
      })
    },
    updated(){

    },mounted(){

    },activated(){
    },
    methods: {
      keyCode(kc) {
        if (kc == "KeyBack") {
          this.back();
        }else if(kc == "right"){
          this.right();
        }else if(kc == "left"){
          this.left();
        }else if(kc == "down"){
          this.down();
        }else if(kc == "up"){
          this.up();
        }else if(kc == "KeyEnter"){
          let url = this.itemList[this.itemNo].jsonUrl;
          this.isShow = false;
          let id = this.itemList[this.itemNo].assetId;
          let pageId =this.initData.data.assetId;
          // bp.categoryClick = function (cat_id, cat_elements, click_type, pageType, pageId, traceId, traceType)
          bi.categoryClick(pageId,id,2,31,pageId);
          console.log(pageId,id,2,31,pageId+'点击事件');
          this.$emit('pressDown', this.isShow,this.name,"KeyEnter",url);
          this.removeCss();
        }
      },
      back() {
        if(this.timer!=0){
          clearInterval(this.timer);
          this.timer = 0;
          let id = '.gameImg li';
          let tab = document.querySelectorAll(id+' .s-title')[this.itemNo];
          document.querySelectorAll(id+' .s-title-temp')[this.itemNo].innerHTML="";
          tab.scrollLeft = 0;
          tab.style.textOverflow = 'ellipsis';
        }
        if(this.$route.query.jsonUrl!=undefined){
          yh.back();
        }else{
          this.$router.go(-1);
        }
      },
      down(){
          if(!this.isEnd){
            this.removeCss();
            this.addCss();
          }
      },
      up(){
        this.removeCss();
        let eleH = this.cEleOffsetTop('#hotImg_'+this.eleId+"_"+this.itemNo+'');
        this.$emit('eleScroll',this.name,'up',eleH);
        this.isShow = false;
        this.$emit('pressDown', this.isShow,this.name,"up");
      },
      left(){
        if(this.itemNo==0 ||this.itemNo==6)return;
        this.removeCss();
        this.itemNo--;
        this.addCss();
      },
      right(){
        if(this.itemNo==5||this.itemNo==11)return;
        this.removeCss();
        this.itemNo++;
        this.addCss();
      },
      addCss(){
        $('#hotImg_'+this.eleId+"_"+this.itemNo).removeClass('tran');
        $('#hotImg_'+this.eleId+"_"+this.itemNo).addClass('tran');
        let p_title = $('.gameImg li:eq('+this.itemNo+') .s-title-name').text();
        if(p_title.length>=9){
          let id = '.gameImg li';
          let speed = 50;
          let that = this;
          let tab =  document.querySelectorAll(id+' .s-title')[this.itemNo];
          let tab1 =  document.querySelectorAll(id+' .s-title-name')[this.itemNo];
          let tab2 =  document.querySelectorAll(id+' .s-title-temp')[this.itemNo];
          tab.style.textOverflow = 'clip';
          tab2.innerHTML = tab1.innerHTML;
          this.timer = setInterval(function(){
            console.log(p_title)
            that.Marquee(tab,tab1,tab2)
          },speed);
        }
      },
      removeCss(){
        $('#hotImg_'+this.eleId+"_"+this.itemNo).addClass('tran');
        $('#hotImg_'+this.eleId+"_"+this.itemNo).removeClass('tran');
        if(this.timer!=0){
          clearInterval(this.timer);
          this.timer = 0;
          let id = '.gameImg li';
          let tab = document.querySelectorAll(id+' .s-title')[this.itemNo];
          document.querySelectorAll(id+' .s-title-temp')[this.itemNo].innerHTML="";
          tab.scrollLeft = 0;
          tab.style.textOverflow = 'ellipsis';
        }
      },
      Marquee(tab,tab1,tab2){
        let a = 0;
        if(tab2.offsetWidth - tab.scrollLeft <= 0){
          // tab.scrollLeft -= tab1.offsetWidth;
          tab.scrollLeft  = 0; // 上面的写法会导致滚动时突然抖动
        }else{
          a = tab.scrollLeft;
          tab.scrollLeft++;
          if(a==tab.scrollLeft){
            tab.scrollLeft=0;
          }
        }
      },
      updateStast(bl,pos,isEnd){
        this.itemNo = 0;
        this.isShow = bl;
        this.pos = pos;
        if(bl){
          this.addCss();
          let eleH = this.cEleOffsetTop('#hotImg_'+this.eleId+"_"+this.itemNo+'');
          this.$emit('eleScroll',this.name,this.pos,eleH);
        }
      },cEleOffsetTop(ele){
        let fEleDistance = $(ele).offset();
        let realHeight = fEleDistance.top+300;
        return realHeight;
      },
      rnd(n,m){
        return parseInt(Math.random()*(m-n)+n);
      }
    },
    components: {

    }
    ,watch:{
      initData(){
        this.specialList = this.initData.data.assetList;
        this.eleId = this.initData.data.assetList[0].assetId;// 默认数据第一个为组件内的id
        this.name = "GameRecom";// 默认数据第一个为组件名字
        this.itemList = this.specialList.sort(function(){ return 0.5 - Math.random() });
      }
    }
  }
</script>




