<!--历史页-->
<template>
  <div class="imgList" style="box-sizing: border-box">
    <ul>
      <li v-for="(item,index) in itemList"  :key="item.index" :id="('hotImg_'+eleId+'_'+index)" class="imgli animated">
         <!--<img :src="item.assetImg" class="imgList_img" >-->
        <lazy-image
          :src='item.assetImg'
          :placeholder='placeholder'
          :initScore = 'initScore'
          :score = "item.score"
          :title = "item.assetName"
          class="imgList_img"
          :imgClass='HistoryImg'
        />
      </li>
    </ul>
    <div class="move" v-if="isLenght">
      <div class="move_box"></div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
  </div>
</template>
<style scoped="center_h">
  .move{
    width: 1px;
    height: 460px;
    background: #fff;
    position: absolute;
    right: 30px;
    top: 40px;
  }
  .move .move_box{
    width: 11px;
    height: 56px;
    background: #fff;
    border-radius: 4px;
    position: absolute;
    left: -5.5px;
    top: 0px;

  }
  /*.imgList ul li .mask{*/
    /*color: black;*/
    /*position: absolute;*/
    /*left: 0;*/
    /*top:0;*/
  /*}*/
  .imgList ul{
    margin-left: 10px;
  }
  .imgList{
    position: relative;
    width: 1200px;
    height: 530px;
    overflow: hidden;
  }
  .imgList ul:after{
    content: '';
    clear: both;
    display: block;
  }
  .imgList ul li{
    width: 160px;
    height:230px;
    float: left;
    margin-right: 32px;
    position: relative;
    margin-top: 28px;
    border-radius: 15px;
    box-sizing: border-box;
    border: 1px rgba(255,255,255, 0.5) solid;
  }
  .imgList .imgList_img{
    width: 100%;
    height: 100%;
    border-radius: 15px;
    box-sizing: border-box;

  }
  .imgList ul .tran{
    border: 4px solid #ffffff;
    transform: scale(1.1);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.55);
  }
</style>
<style>
  .HistoryImg{
    width: 100%;
    height: 100%;
    border-radius: 15px;
    box-sizing: border-box;
  }
</style>
<script>
  import { mapGetters } from 'vuex'
  import {client as yh} from '../js/client.js'
  import {bp as bi} from '../js/ga.js'
  import lodash from 'lodash'

  export default {
    props:['type','catId','catCode'],
    data(){
      return{
        num:0,
        itemNo:0,
        isShow:true,
        name:'HCenter',
        isLenght:false,
        placeholder:require('../assets/img_loading_160x230.png'),
        initScore:require('../assets/corner_mark.png'),
        eleId:'',
        pos:'',
        isEnd:false,
        HistoryImg:'HistoryImg',
        itemList:[],
        aarr:[{"assetId":"6518","assetImg":"http://183.131.15.11/img/201705/03/14/28/20170503142856048e0061a6f.jpg","assetName":"街头霸王：追求武","assetType":"Column","broadcast":"","episode":"1","episodes":"36","id":1,"itemId":"81422","itemImg":"http://183.131.15.11/img/201705/03/12/54/2017050312540749391ce8479.jpg","itemName":"第01期系统教学","layout":"Detail_News","layoutUrl":"http://47.97.96.103/?s=22|4&p=yhAssetDetail&k=1&v=1&assetId=6518&c=4","movieLength":"1291200","playurl":"http://vcdn.yanhuamedia.tv/vod/201705/03/12/51/201705031251084692a5d45db.ts","recordTime":1538275775844,"score":"6.5","specialId":"","watchTime":"11880"},      
],
        historyList:[],
        timer:null,
      }
    },
    computed: {

    },
    created(){
      this.itemList = JSON.parse(yh.queryHistory());
      // this.itemList = this.aarr
      console.log('历史记录'+JSON.stringify(yh.queryHistory()))
      this.changeJson(this.itemList);
      if(this.itemList.length>11){
        this.isLenght = true;
      }
      this.$nextTick(function() {
        this.$lazyImages.loadImage();
      })
//      线上
    },
    mounted(){
      this.addCss();
    },
    methods: {
      keyCode(kc) {
        console.log('按键触发了'+ kc)
        if (kc == "KeyBack") {
          console.log('返回按键触发了'+ kc)
          this.back();
        }else if(kc == "right"){
          this.right();
        }else if(kc == "left"){
          this.left();
        }else if(kc == "down"){
          this.down();
        }else if(kc == "up"){
          this.up();
        }else if(kc == "KeyEnter"){
          console.log('this.itemList:'+this.itemList)
          // if(this.itemLis.length<=0)return;
          this.isShow = false;
            let url = this.itemList[this.itemNo].layoutUrl;
            let Id =this.itemList[this.itemNo].assetId;
            this.assetId = Id;
            console.log(this.name);
            console.log(url);
            this.$emit('pressLeft',this.isShow,this.name,"KeyEnter",url,Id,'详情',"1|5");
            this.typeid = 1;
            console.log("点击时候的typeid"+this.typeid);
            this.collectPage();
            this.removeCss();
        }
      },
      //      点击事件
      collectPage(){
        this.traceId = "|"+this.catId+"|"+0;
        console.log(this.traceId,'|1|6|',6,0,1,this.catId,this.assetId,this.traceId,1,0,this.typeid);
        bi.historyClick(this.assetId,this.typeid,this.traceId,"|1|6|",6,0,1,this.catId);
      },
      gundong(){
        let number = Math.ceil(this.itemList.length/12);
        let ly =Math.ceil(460/(number-1)-23);
        $('.move_box').css({'transition': 'all 0.4s', 'transform': 'translateY('+ ly*this.num +'px)'});
      },
      back() {
        this.clearTimer();
        console.log('返回了')
        this.$router.go(-1);
      },
      down:lodash.debounce(function(){
       let eleH = this.cEleOffsetTop('#hotImg_'+this.eleId+"_"+this.itemNo+'');
       console.log('元素高度'+eleH)
       console.log('itemNo'+this.itemNo)
       console.log('数组长度'+this.itemList.length)
        if(this.itemNo<=this.itemList.length-7) {
          this.removeCss();
          this.itemNo += 6;
          this.addCss();
          if(eleH>600){
            this.num++;
            this.$emit('pressDown', this.isShow,this.name,"up",this.num+1);
            this.gundong();
            $('.imgList ul').stop(false,true).animate({'margin-top': '-='+520},100);
          }
        }else {
          if(eleH>600 && this.itemNo+1<(this.num+1)*12){ 
          alert('需要翻页了')
          console.log('下')
          this.num++;
          this.$emit('pressDown', this.isShow,this.name,"up",this.num+1);
          this.gundong();
          $('.imgList ul').stop(false,true).animate({'margin-top': '-='+520},100);
          }
          this.removeCss();
          this.itemNo = this.itemList.length-1;
          this.addCss();
        }
      },300),
      up:lodash.debounce(function(){
        this.removeCss();
        let eleH = this.cEleOffsetTop('#hotImg_'+this.eleId+"_"+this.itemNo+'');
        if(this.itemNo>5){
          this.itemNo-=6;
          this.addCss();
          if(eleH<500){
            this.num--;
            this.$emit('pressDown', this.isShow,this.name,"up",this.num+1);
            this.gundong();
            $('.imgList ul').stop(false,true).animate({'margin-top': '+='+520},100);
          }
        }else{
          this.isShow = false;
          this.$emit('pressDown', this.isShow,this.name,"up",this.num+1);
        }
      },300),
      left:lodash.debounce(function(){
        if(this.itemNo==0 ||this.itemNo==6 )return;
        this.removeCss();
        this.itemNo--;
        this.addCss();
      },300),
      right:lodash.debounce(function(){
        if(this.itemNo==5||this.itemNo==11 || this.itemNo>=this.itemList.length-1)return;
        this.removeCss();
        this.itemNo++;
        this.addCss();
      },300),
      addCss(){
//        $('#hotImg_'+this.eleId+"_"+this.itemNo).removeClass('tran');
        $('#hotImg_'+this.eleId+"_"+this.itemNo).addClass('tran');
        this.scrollName();
      },removeCss(){
//        $('#hotImg_'+this.eleId+"_"+this.itemNo).addClass('tran');
        $('#hotImg_'+this.eleId+"_"+this.itemNo).removeClass('tran');
        this.clearTimer();
      },
      scrollName(){
        let p_title =  $('#hotImg_'+this.eleId+"_"+this.itemNo + ' .s-title .s-title-name').text();
        if(p_title.length>=8){
          let speed = 80;
          let that = this;
          let tab =  $('#hotImg_'+this.eleId+"_"+this.itemNo + ' .s-title')[0];
          let tab1 =  $('#hotImg_'+this.eleId+"_"+this.itemNo + ' .s-title .s-title-name')[0];
          let tab2 =  $('#hotImg_'+this.eleId+"_"+this.itemNo + ' .s-title .s-title-temp')[0];
          tab.style.textOverflow = 'clip';
          tab2.innerHTML = tab1.innerHTML;
          this.timer = setInterval(function(){
            console.log('要滚动的文字 :'+p_title)
            that.Marquee(tab,tab1,tab2)
          },speed);
        }
      },
      Marquee(tab,tab1,tab2){
        let a = 0;
        if(tab2.offsetWidth - tab.scrollLeft <= 0){
          // tab.scrollLeft -= tab1.offsetWidth;
          tab.scrollLeft  = 0; // 上面的写法会导致滚动时突然抖动
        }else{
          a = tab.scrollLeft;
          tab.scrollLeft++;
          if(a==tab.scrollLeft){
            tab.scrollLeft=0;
          }
        }
      },
      clearTimer(){
        try{
          if(this.timer!=null){
            clearInterval(this.timer);
            this.timer = 0;
            let tab = $('#hotImg_'+this.eleId+"_"+this.itemNo + ' .s-title')[0];
            $('#hotImg_'+this.eleId+"_"+this.itemNo + ' .s-title .s-title-temp')[0].innerHTML="";
            tab.scrollLeft = 0;
            tab.style.textOverflow = 'ellipsis';
          }          
        }catch(error){
          console.log('清除定时器失败')
        }
      },
      updateStast(bl,pos,name){
        //线上
        this.itemList = JSON.parse(yh.queryHistory());
        // this.itemList = this.aarr
        this.changeJson(this.itemList);
        if(this.itemList.length<=0){
          // 历史记录被删除完后需要将焦点移动到父组件的'清空记录'按钮上
          console.log('===================================')
          this.$emit('pressDown', this.isShow,this.name,"up",1);
          return
        }
//        测试
      //  this.getquery(this.favoriteList);
//        console.log(this.favoriteLists);
        this.removeCss();
        this.num = 0;
        this.itemNo = 0;
        this.addCss();
        this.isShow = bl;
        this.gundong();
        $('.imgList ul').stop(false,true).animate({'margin-top': 0},100);
        console.log(pos);
        if (name == 'KeyEnter'){
          this.typeid = 2;
          console.log("删除收藏时候的typeid"+this.typeid);
          this.collectPage();
        };
      },
      cEleOffsetTop(ele){
        let fEleDistance = $(ele).offset();
        let realHeight = fEleDistance.top+300;
        return realHeight;
      },
      changeJson(jsonDB){
        this.historyList = jsonDB;
        let _tempArray = [];
        let _this = this;//  sort 作用域在windows下面需要注意
        let sortedObjKeys = Object.keys(this.historyList).sort(function(a, b) {
          return _this.historyList[b].recordTime - _this.historyList[a].recordTime;
        });
        for (let i in sortedObjKeys) {
          if (sortedObjKeys.hasOwnProperty(i)) {
              _tempArray.push(this.historyList[sortedObjKeys[i]]);
          }     
        }
        this.itemList  = _tempArray;
        console.log(this.historyList);

      },
      again(bl){
        if (!bl){
          this.$emit('pressDown', this.isShow,this.name,"up",this.num+1);
          yh.removeHistory();
          this.$emit('deleteMessg',true);
          this.itemList = JSON.parse(yh.queryHistory());
          // this.itemList = this.aarr
          this.changeJson(this.itemList);
        }
      },
    },
    components: {

    },
    watch:{
      initData(){
        this.num = 0;
        this.itemNo = 0;
        this.itemList = JSON.parse(yh.queryHistory());
        // this.itemList = this.aarr
        this.changeJson(this.itemList);
        this.eleId = this.itemList[0].assetId;// 默认数据第一个为组件内的id
        this.name = 'HCenter';// 默认数据第一个为组件名字
      },
      itemList(){
        if(this.itemList.length <= 0){
          this.$emit('deleteMessg',true);
        }else{
          this.$emit('deleteMessg',false);
        }
      }
    }
  }
</script>




