// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import "babel-polyfill"
import Vue from 'vue'
import App from './App'
import router from './router'
import VueLazyImage from '../src/tool/lazyLoad/index.js'
import keyDo from '../src/components/keyCode/index.js'
import store from './store/store'
import LFirm from '../src/tool/Firm/index.js'
import lodash from 'lodash'

Vue.config.productionTip = false;
Vue.use(keyDo);
Vue.use(VueLazyImage,{debounce:200});
Vue.use(LFirm);
Vue.use(lodash);


router.afterEach((to,from)=>{
  if (from.name!=null){
    store.commit('GET_STARTTIME', new Date().getTime());
  }
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



