<!--详情页——游戏类详情页顶部-->
<style scoped="Mtop">
  .Rectangle{
    width:1200px;
    height: 260px;
  }
  .Rectangle:after{
    clear: both;
    content: "";
    display: block;
  }
  .Rectangle .Mask{
    border-radius:15px;
    border: #707070 1px solid;
    width:181px;
    height:260px;
    float: left;
    position: relative;
  }
  .Rectangle .Mask img{
    width: 100%;
    height: 100%;
    border-radius: 12px;
    border:2px solid #ffffff;
    box-sizing: border-box;
  }
  .Bona{
    width: 700px;
    margin-left: 45px;
    float: left;
  }
  .Bona .text-page span:nth-child(1){
    font-family:PingFangSC-Semibold;
    font-size:32px;
    color:#ececec;
    text-align:left;
    margin-right: 5px;
  }
  .Bona .text-page span:nth-child(2){
    font-family:PingFangSC-Medium;
    font-size:20px;
    color:#f5a623;
    text-align:left;
  }
  .Bona .text-page2 {
    font-family:PingFangSC-Regular;
    font-size:20px;
    color:#ffffff;
    text-align:left;
    margin-top: 16px;
  }
  .Bona .text-page2 span{
    text-indent: 10px;
    display:block;
    margin-right: 10px;
    opacity: 0.5629;
  }
  .Bona  .content-page3{
    font-family:PingFangSC-Regular;
    text-indent: 10px;
    font-size:22px;
    line-height: 28px;
    color:#ffffff;
    text-align:left;
    margin-top: 16px;
    overflow : hidden;
    position: relative;
    /* text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical; */
    opacity: 0.8;
  }
  .shenlue::after{
    content: "...";
    position: absolute;
    bottom: 0;
    right: 0;
    padding-right: 27px;
      background: -webkit-linear-gradient(left, #3B273E, #3B273E 55%);
      background: -o-linear-gradient(right, #3B273E, #3B273E 55%);
      background: -moz-linear-gradient(right, #3B273E, #3B273E 55%);
      background: linear-gradient(to right, #3B273E, #3B273E 55%);
  }
  .btn-page{
    width: 600px;
    position: absolute;
    left: 230px;
    top: 216px;
  }
  .btn-page div:nth-child(1){
    border-radius: 15px 0 0 15px;
    background:rgba(255,255,255,0.2);
    font-family:PingFangSC-Regular;
    font-size:20px;
    color:#ffffff;
    text-align:center;
    float: left;
    line-height: 46px;
    width: 108px;
    height: 45px;
    margin-right: 4px;
  }
  .btn-page div:nth-child(2){
    border-radius: 0 15px 15px 0;
    background:rgba(255,255,255,0.2);
    font-family:PingFangSC-Regular;
    font-size:20px;
    color:#ffffff;
    text-align:center;
    float: left;
    line-height: 46px;
    width: 108px;
    height: 45px;

  }
  .oll-page li{
    font-family:PingFangSC-Regular;
    font-size:18px;
    color:#ffffff;
    float: left;
    padding-left: 42px;
    padding-top: 14px;
  }
  .Rectangle .btn-page .change{
    background: #A36363;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.55);
    border: solid 4px white;
    box-sizing: border-box;
    line-height: 38px;
  }

</style>
<template>
  <div class="Rectangle" style="box-sizing: border-box;">
    <div class="Mask">
      <img :src=(art.assetImg) >
    </div>
    <div class="Bona">
      <p class="text-page">
        <span>{{art.assetName}}</span>
        <span>{{art.score}}</span>
      </p>

      <p class="text-page2">
        <span>{{art.year}}</span>
      </p>
      <p class="content-page3">{{art.description}}</p>
      <div class="btn-page">
        <div class="btn-play">
          <i class="iconfont">&#xe607 </i>
          播放</div>
        <div class="btn-sc">
          <span v-if="sc"><i class="iconfont" v-if="sc">&#xe658    </i></span>   <span v-if="sc">已</span><i class="iconfont" v-if="ss">&#xe617    </i>收藏
        </div>
        <!--<div class="btn-js "><i class="iconfont">&#xe60b    </i>介绍</div>-->
      </div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
  </div>
</template>



<script>
  import {client as yh} from '../js/client.js'
  import { mapGetters } from 'vuex'
  import {bp as bi} from '../js/ga.js'

  export default {
    props:['jsonUrl','initData',"catCode","catId","parent","parentId"],
    data() {
      return {
        name:'GameTop',
        num:1,
        sc:false,
        ss:true,
        isShow:false,
        pos:'',
        str:'',
        art: {
          },
        collect:
          {
            asType:'',
            asimg:'',
            asname:'',
            sco:'',
            asId:''
          },
      }
    },
    components: {
    },
    computed: {
      ...mapGetters([
        'behindParams',
      ])
    },
    mounted(){
      this.shenluehao()
    },
    created(){
      this.art = this.initData.data;
      if (this.queryFavorite()){
        this.sc = true;
        this.ss = false
      }else {
        this.sc = false;
        this.ss = true;
      }
    },
    methods: {
      keyCode(kc) {
        if (kc == "down") {
          this.down();
        } else if (kc == "left") {
          this.left();
        } else if (kc == "right") {
          this.right();
        } else if (kc == "KeyEnter") {
          this.KeyEnter();
        }else if(kc == "KeyBack"){
          this.back();
        }
      },
      /**
       * 查看收藏
       * @param {assetId,specialId} params
       * assetId:资产id
       * specialId:专题id
       */
      queryFavorite(){
        let storage =JSON.parse(localStorage.getItem('PersonInfo'));
        for (let i=0; i<storage.length; i++){
          if(storage[i].assetId ==this.art.assetId){
            return true;
          }
        }
      },
      KeyEnter() {
        if (this.num==2){
          if (!this.sc) {
            this.sc=true;
            this.ss = false;
//        * 添加收藏
//          * @param {obj} params
//          * 包含：String catId,String specialId,String assetId,
//                String assetName,
//                String assetType,
//                String assetImg,
//                String episodes,
//                String broadcast,
//                String score,
//                String coverImg,
//                String specialName,
//                String layout,
//                String layoutUr
//          */
            let Tvsc =
              {
                assetId:this.art.assetId,
                assetName:this.art.assetName,
                assetType:this.art.assetType,
                assetImg:this.art.assetImg,
                episodes:this.art.episodes,
                broadcast:this.art.broadcast,
                score:this.art.score,
                coverimage:this.art.coverimage,
                specialName:this.art.specialName,
                layout:this.art.layout,
                layoutUrl:this.jsonUrl
              };
            yh.collection(JSON.stringify(Tvsc));
            let storage =localStorage.getItem('PersonInfo');
            if (storage) {
              storage = JSON.parse(storage);
              } else {
              storage = [];
             }
            storage.push(Tvsc);
            localStorage.setItem( 'PersonInfo',JSON.stringify( storage ));
            this.Coll();
          }else {
            this.sc=false;
            this.ss = true;
            let storage =JSON.parse(localStorage.getItem('PersonInfo'));
            let json =[];
            for(let i = 0;i<storage.length;i++){
              if(i !=this.findindex()){
                json.push(storage[i]);
              }
            };
            localStorage.setItem( 'PersonInfo',JSON.stringify( json ));
//          * 删除收藏
//          * @param {*} assetId
//          * @param {*} specialId
//          *
            yh.removeFavorite(this.art.assetId,'');
            this.NoColl();
          }
        }
        else if(this.num==1){
          let id = this.initData.data.itemList[0].itemId;
          let pageId =this.initData.data.assetId;
          let traceId = this.catId;
          let traceType = this.catCode;
          //      bp.categoryClick = function (cat_id, cat_elements, click_type, pageType, pageId, traceId, traceType)
          bi.categoryClick(this.catId,id,5,31,pageId,traceId,traceType);
          console.log(this.catId,id,5,31,pageId,traceId,traceType+'点击事件');
          let obj = JSON.stringify({index:0,startTime:0,jsonUrl:this.jsonUrl});
          yh.play(obj);
        }
      },
      shenluehao(){
        if(this.art.description.length >92){
            let str = this.art.description.substring(0,92)
            $('.content-page3').text(str+'  ...')
        }else{
            $('.content-page3').text(this.art.description)
        }
      },
      right() {
        this.num+=1;
        if (this.num==1){
          $('.btn-play').addClass('change');
          $('.btn-sc').removeClass('change');
        }else if (this.num==2){
          $('.btn-sc').addClass('change');
          $('.btn-play').removeClass('change');
          this.num = 2;
        }
      }, left() {
        this.num-=1;
        if (this.num==2){
          $('.btn-sc').addClass('change');
          $('.btn-play').removeClass('change');
        }else if (this.num<=1){
          $('.btn-play').addClass('change');
          $('.btn-sc').removeClass('change');
          this.num=1
        }
//        else if (this.num==3){
//          $('.btn-sc').removeClass('change');
//          $('.btn-js').addClass('change');
//          $('.btn-play').removeClass('change');
//        }
      }, back() {
        if(this.$route.query.jsonUrl!=undefined){
          yh.back();
        }else{
          this.$router.go(-1);
        }
      },
      down(){
        $('.btn-sc').removeClass('change');
        $('.btn-play').removeClass('change');
        this.isShow = false;
        this.$emit('pressDown', this.isShow,this.name,"down");
      },
      updateStast(bl,pos,higth){
        this.isShow = bl;
        this.pos = pos;
        if(bl){
          this.num=1;
          $('.btn-play').addClass('change');
          let eleH = this.cEleOffsetTop('.btn-sc');
          this.$emit('eleScroll',this.name,pos,eleH);
        }
      },cEleOffsetTop(ele) {
        let fEleDistance = $(ele).offset();
        let realHeight = fEleDistance.top + 70;
        return realHeight;
      },popUp(bOk) {

      },
      findindex(){
        let storage =JSON.parse(localStorage.getItem('PersonInfo'));
        for(let i = 0;i<storage.length;i++){
          if (storage[i].assetId == this.art.assetId){
            return i;
          }
        }
      },
      Coll(){
        let pageId =this.art.assetId;
        let assId = this.art.itemList[0].itemId;
        let traceId =  this.catId;
        let traceType = this.catCode;
//        bp.collect = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id, cat_id, asset_id, collect_type, type)
        bi.collect(traceId,traceType,31,pageId,traceType,this.catId,pageId,assId,3,1);
        console.log(traceId,traceType,31,pageId,traceType,this.catId,pageId,assId,3,1);
      },
      NoColl(){
        let pageId =this.art.assetId;
        let assId = this.art.itemList[0].itemId;
        let traceId =  this.catId;
        let traceType =  this.catCode;
//        bp.collect = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id, cat_id, asset_id, collect_type, type)
        bi.collect(traceId,traceType,31,pageId,traceType,this.catId,pageId,assId,3,2);
        console.log(traceId,traceType,31,pageId,traceType,this.catId,pageId,assId,3,2);
      }
    },watch:{
      initData(){
        this.art = this.initData.data;
        this.isShow = false;
        this.shenluehao();
        $('.btn-play').removeClass('change');
        if (this.queryFavorite()){
          this.sc = true;
          this.ss = false
        }else {
          this.sc = false;
          this.ss = true;
        }
      }
    }
  }
</script>



