import fetchJsonp from 'fetch-jsonp';

/**
 * 2017-11-30
 * 基于fetch封装get jsonp请求请求
 * 
 */

var HTTPUtil = {};


HTTPUtil.get =  function (url) {
  // console.log("url:" + url);
  return new Promise(function (resolve, reject) {
    fetchJsonp(url, {
      jsonpCallback: 'jsonpCallback',
    })
      .then(function (response) {
        if (response.ok) {
          return response.json();
        } else {
          reject({status:response.status });
        }
      })
      .then(function (response) {
        resolve(response);
      })
      .catch(function (ex) {
        console.log(ex);
        reject({ status: -1 });  
      })
  })
}

// 获取主题接口
HTTPUtil.getTheme = function(url) {
  return  HTTPUtil.get(url);
}

// 获取栏目筛选条件
HTTPUtil.getFilter = function(catCode){
  // let url = "https://183.131.15.11/?s=12|4&p=yhCategoryList&k=1&v=1&catCode=game&returnType=jsonp";
  let url = "http://183.131.15.11:8490/?s=22|4&p=yhCategoryList&k=1&v=1&catCode=game&returnType=jsonp";
  return HTTPUtil.get(url);
} 

// 获取栏目筛选结果
HTTPUtil.getFilterResult = function (condition, index, pageSize,catCode) {
  // let url = "https://183.131.15.11/?s=12|4&p=yhScreenResult&k=1&v=1&returnType=jsonp&content=" + condition + "&pindex=" + index + "&psize=" + pageSize + "&catCode=game";
  let url = "http://183.131.15.11:8490/?s=22|4&p=yhScreenResult&k=1&v=1&returnType=jsonp&content=" + condition + "&pindex=" + index + "&psize=" + pageSize + "&catCode=game";
  return HTTPUtil.get(url);
} 



export  {HTTPUtil};



